/**
 * Match initial spaces in a string.
 * @private
 * @type {RegExp}
 */
const START_SPACES_REGEX = /^\s{2,}/g;
/**
 * Match spaces between tags.
 * @private
 * @type {RegExp}
 */
const TAG_SPACES = /\>\s+\</g;
/**
 * Match opening and closing tags.
 * @private
 * @type {RegExp}
 */
const TAG_REGEX = /(<\/?([a-zA-Z1-9\-]*)([^>]*)>?)/m;
/**
 * Match tag attributes.
 * @private
 * @type {RegExp}
 */
const PROPS_REGEX = /((?:.|\n)*?)(?:\/?>|<|$)/;
/**
 * Match unwanted characters in tag attribute definition.
 * @private
 * @type {RegExp}
 */
const CLEAN_PROPS_STR = /[\'\"\/\s]/;
/**
 * A list of self closing HTML tags.
 * @private
 * @type {Array}
 */
const SELF_CLOSING_TAGS = [
    'area',
    'base',
    'br',
    'col',
    'command',
    'embed',
    'hr',
    'img',
    'input',
    'keygen',
    'link',
    'meta',
    'param',
    'source',
    'track',
    'wbr',
];
/**
 * Match self closing tags.
 * @private
 * @type {RegExp}
 */
const CLOSED_TAG = /\/>$/;

export class Parser {
    /**
     * String HTML parser for Incremental DOM rendering.
     * @param {String} data The text to parse.
     */
    constructor(data) {
        /**
         * Data to parse.
         * @type {String}
         */
        this.data = data
            .replace(/\n/g, '')
            .replace(TAG_SPACES, '><');
        this.restart();
    }
    /**
     * Reset parser index.
     */
    restart() {
        this.setCurrent(this.data);
    }
    /**
     * Set data for the next parsing step.
     * @private
     * @param {String} data
     */
    setCurrent(data) {
        if (typeof data === 'string') {
            /**
             * Current chunk to parse.
             * @type {String}
             */
            this.current = data
                .replace(START_SPACES_REGEX, ' ');
        }
    }
    /**
     * Check if all data has been parsed.
     * @return {Boolean}
     */
    ended() {
        return !this.current;
    }
    /**
     * Get the next chunk analysis.
     * @return {Object} The anlysis descriptor.
     */
    next() {
        let res = false;
        if (!this.ended()) {
            let match;
            let current = this.current;
            if (current[0] !== '<') {
                if (current[0] === '>' || (current[0] === '/' && current[1] === '>')) {
                    let selfClosing = false;
                    if (current[0] === '/') {
                        match = [current, '/>'];
                        selfClosing = true;
                    } else {
                        match = [current, '>'];
                    }
                    res = {
                        chunk: current,
                        selfClosing,
                        type: Parser.OPEN_ELEMENT_END,
                    };
                } else {
                    match = current.match(PROPS_REGEX);
                    res = {
                        chunk: current,
                        type: Parser.CONTENT,
                        content: match[1],
                    };
                }
            } else {
                match = current.match(TAG_REGEX);
                if (current[1] === '/') {
                    res = {
                        chunk: current,
                        type: Parser.CLOSE_ELEMENT,
                        tag: match[2],
                    };
                } else {
                    let longMatch = match[1];
                    if (longMatch[longMatch.length - 1] === '>') {
                        res = {
                            chunk: current,
                            type: Parser.OPEN_ELEMENT,
                            continue: false,
                            tag: match[2],
                            selfClosing: Parser.isSelfClosing(match[2]) ||
                                Parser.isSelfClosing(longMatch),
                            props: Parser.convertProps(match[3], true),
                        };
                    } else {
                        res = {
                            chunk: current,
                            type: Parser.OPEN_ELEMENT,
                            continue: true,
                            tag: match[2],
                            selfClosing: Parser.isSelfClosing(match[2]),
                            props: Parser.convertProps(match[3]),
                        };
                    }
                }
            }
            if (match && match[1] !== current) {
                this.setCurrent(current.substring(match[1].length));
            } else {
                this.setCurrent('');
            }
        }
        return res;
    }
    /**
     * Opening element state key.
     * @type {String}
     */
    static get OPEN_ELEMENT() {
        return 'OPEN_ELEMENT';
    }
    /**
     * Closing element definition state key.
     * @type {String}
     */
    static get OPEN_ELEMENT_END() {
        return 'OPEN_ELEMENT_END';
    }
    /**
     * Close element state key.
     * @type {String}
     */
    static get CLOSE_ELEMENT() {
        return 'CLOSE_ELEMENT';
    }
    /**
     * Content state key.
     * @type {String}
     */
    static get CONTENT() {
        return 'CONTENT';
    }
    /**
     * Check if a string matches a self closing tag.
     * @param {String} str The string to check.
     * @return {Boolean}
     */
    static isSelfClosing(str) {
        return SELF_CLOSING_TAGS.indexOf(
            str.toLowerCase()
        ) !== -1 || !!str.match(CLOSED_TAG);
    }
    /**
     * Convert an HTML text with attributes to key/value object.
     * @param {String} str The string to parse.
     * @param {*} defaultEmpty The default value for empty attributes.
     * @return {Object} A key/value object representation for HTML attributes.
     */
    static convertProps(str = '', defaultEmpty = false) {
        let res = {};
        if (str) {
            let isOpenAttr = false;
            let isOpenApix = false;
            let isOpenValue = false;
            let attrName = null;
            for (let i = 0, len = str.length; i < len; i++) {
                let ch = str[i];
                if (ch === '=') {
                    if (!isOpenAttr) {
                        attrName = undefined;
                    }
                    res[attrName] = false;
                    isOpenAttr = false;
                    isOpenValue = true;
                } else if (ch.match(/[\s|\n]/)) {
                    if (isOpenApix) {
                        res[attrName] = res[attrName] || '';
                        res[attrName] += ch;
                    } else if (isOpenAttr) {
                        if (attrName) {
                            res[attrName] = true;
                            attrName = null;
                            isOpenAttr = false;
                        }
                    } else if (isOpenValue) {
                        attrName = null;
                        isOpenValue = false;
                    }
                } else if ((ch === '"' || ch === '\'') && attrName !== null) {
                    if (isOpenValue && res[attrName] !== false) {
                        attrName = null;
                        isOpenAttr = false;
                        isOpenApix = false;
                    } else {
                        isOpenApix = true;
                        res[attrName] = res[attrName] || '';
                    }
                } else if (isOpenValue) {
                    res[attrName] = res[attrName] || '';
                    res[attrName] += ch;
                } else {
                    isOpenAttr = true;
                    attrName = attrName || '';
                    if (attrName !== '' || !ch.match(CLEAN_PROPS_STR)) {
                        attrName += ch;
                    }
                }
            }
            if (isOpenAttr && attrName) {
                res[attrName] = defaultEmpty;
            } else if (isOpenApix) {
                res[attrName] = false;
            }
        }
        return res;
    }
}
