/**
 * Escape regex body.
 * @private
 * @param {String} str The regex body.
 * @return {String} The escaped regex body.
 */
function escapeRegExpBody(str) {
    return str.replace(/\$/g, '\\$');
}
/**
 * Generate a RegExp with rules.
 * @private
 * @param {Array} A list of rules.
 * @return {RegExp} The RegExp for the given rules.
 */
function getRule(rules) {
    let regex = new RegExp(`^(${
        rules.map((str) =>
            escapeRegExpBody(str)
        ).join('|')
    })`);
    return regex;
}

export class Interpolator {
    /**
     * Interpolate text and get notified about matches.
     * @param {Object} options The interpolator configuration.
     * @property {Array} options.start A list of rules for interpolation start.
     * @property {Array} options.end A list of rules for interpolation end.
     */
    constructor(options) {
        /**
         * A regex which matches start symbols.
         * @type RegExp
         */
        this.start = getRule(options.start);
        /**
         * A regex which matches end symbols.
         * @type RegExp
         */
        this.end = getRule(options.end || options.start);
        this.contextStart = getRule(['{']);
        this.contextEnd = getRule(['}']);
    }
    /**
     * Iterate a text and match interpolations.
     * @param {String} body The text to iterate.
     * @param {Function} notify The callback function for interpolation matches.
     */
    interpolate(body, notify) {
        let {
            start,
            end,
            contextStart,
            contextEnd,
        } = this;
        let len = body.length;
        let i = 0;
        let chunk = '';
        let contexts = 0;
        while (i < len) {
            let ch = body[i];
            let sliced = body.slice(i);
            if (contexts === 0 && sliced.match(start)) {
                contexts = 1;
                if (chunk) {
                    notify({
                        interpolate: false,
                        chunk,
                    });
                    chunk = '';
                }
                i += sliced.match(start)[0].length;
                continue;
            } else if (contexts === 1 && sliced.match(end)) {
                i += sliced.match(end)[0].length;
                notify({
                    interpolate: true,
                    chunk,
                });
                chunk = '';
                contexts = 0;
                continue;
            } else if (sliced.match(contextEnd)) {
                if (contexts > 1) {
                    contexts--;
                }
                /* istanbul ignore next */
                /* String literal's interpolation end rule is equal to the context end one,
                 * so we are not really handling this case.
                 */
                if (contexts === 1 && i === (len - 1)) {
                    notify({
                        interpolate: true,
                        chunk,
                    });
                    chunk = '';
                }
            } else if (sliced.match(contextStart)) {
                if (contexts > 0) {
                    contexts++;
                }
            }
            chunk += ch;
            i++;
        }
        if (chunk) {
            notify({
                interpolate: contexts >= 1,
                chunk,
            });
        }
    }
}
