import { IDOM } from './idom.js';
import { LiteralCompiler } from './compilers.js';

export class Template {
    static get IDOM() {
        return IDOM;
    }

    constructor(...vars) {
        let template = vars.pop();
        if (template instanceof Template) {
            this.factory = template.factory;
            this.scope = template.scope;
        } else if (typeof template === 'function') {
            this.factory = template;
        } else {
            this.factory = new LiteralCompiler(template).compile(...vars);
        }
    }

    setScope(scope) {
        this.scope = scope;
        return this;
    }

    render(node, ...args) {
        IDOM.patch(
            node,
            () => this.factory.apply(this.scope || null, args)
        );
        return this;
    }
}
