import { Render } from './render.js';
import { Interpolator } from './interpolator.js';
import { IDOM } from './idom.js';

/**
 * The variable name for incremental DOM API in generated function.
 * @private
 * @type {String}
 */
const SKIN_VAR = '$$$skin';
/**
 * The variable name for original context in generated function.
 * @private
 * @type {String}
 */
const SKIN_SCOPE = '$$$this';
/**
 * The variable name for original arguments in generated function.
 * @private
 * @type {String}
 */
const SKIN_ARGS = '$$$arguments';
/**
 * An interpolator for function body.
 * @private
 * @type {Interpolator}
 */
const FUNCTION_INTERPOLATOR = new Interpolator({
    start: ['`'],
});
/**
 * An interpolator for literal body.
 * @private
 * @type {Interpolator}
 */
const LINEAR_INTERPOLATOR = new Interpolator({
    start: ['${'],
    end: ['}'],
});

/**
 * A generic Compiler class.
 *
 * @example
 * ```js
 * let compiler = new Compiler('<div class="sample">${name}</div>');
 * let fn = compiler.compile('name');
 * IncrementalDOM.patch(document.body, fn('Alan'));
 * ```
 */
class Compiler {
    /**
     * Create a Compiler.
     * @param {String} body The template text to compile.
     * @param {Compiler} parent The parent compiler.
     */
    constructor(body, parent) {
        /**
         * The compiler which has instantiated this.
         * @type {Compiler}
         */
        this.parent = parent;
        /**
         * The render helper for the compiler.
         * @type {Render}
         */
        this.render = (parent instanceof Compiler) ? parent.render : new Render({
            helper: SKIN_VAR,
            scope: SKIN_SCOPE,
            args: SKIN_ARGS,
        });
        /**
         * The body to compile.
         * @type {String}
         */
        this.body = body;
    }
    /**
     * Generate a function script.
     * @param {String} body The body of the function.
     * @param {Array} vars A list of global variables to inject into the function.
     * @return {String} The function script
     */
    generate(vars, body) {
        body = body || this.body;
        return `function(${SKIN_VAR}${vars.length ? `, ${vars.join(', ')}` : ''}) { ${body} }`;
    }
    /**
     * Compile the body to a function.
     * @param {String} ...vars A list of global variables to inject into the function.
     * @return {Function} The compiled template function.
     */
    compile(...vars) {
        let body = this.generate(vars);
        try {
            let fn = new Function(
`var ${SKIN_SCOPE} = this;
var ${SKIN_ARGS} = arguments;
(${body}).apply(${SKIN_SCOPE}, ${SKIN_ARGS});`
            );
            let res = function(...args) {
                return fn.call(
                    this,
                    IDOM,
                    ...args
                );
            };
            res.render = fn;
            return res;
        } catch (ex) {
            let original = this.body;
            return function() {
                let err = new Error(`Malformed template. ${original} => ${body}`);
                err.original = ex;
                throw err;
            };
        }
    }
}

/**
 * A compiler for function body.
 * @extends Compiler
 */
export class FunctionCompiler extends Compiler {
    /**
     * Split the function body in chunks.
     * @return {Array} A list of script strings and {@link LiteralCompiler} instances.
     */
    chunk() {
        let chunks = [];
        FUNCTION_INTERPOLATOR.interpolate(this.body, (desc) => {
            if (desc.interpolate) {
                chunks.push(new LiteralCompiler(desc.chunk, this));
            } else {
                chunks.push(desc.chunk);
            }
        });
        return chunks;
    }
    /**
     * @see {@link Compiler}
     * @private
     */
    generate(vars) {
        let res = 'return ';
        let chunks = this.chunk();
        chunks.forEach((chunk, index) => {
            if (chunk instanceof LiteralCompiler) {
                res += this.render.interpolate(chunk, vars);
            } else {
                if (index === 0) {
                    chunk = chunk.replace(/^\s*/, '');
                }
                res += `${chunk}`;
            }
        });
        res += ';';
        return super.generate(vars, res);
    }
}

/**
 * A compiler for literal body.
 * @extends Compiler
 */
export class LiteralCompiler extends Compiler {
    /**
     * Split the literal body in chunks.
     * @return {Array} A list of text strings and {@link FunctionCompiler} instances.
     */
    chunk() {
        let chunks = [];
        LINEAR_INTERPOLATOR.interpolate(this.body, (desc) => {
            if (desc.interpolate) {
                chunks.push(new FunctionCompiler(desc.chunk, this));
            } else {
                chunks.push(desc.chunk);
            }
        });
        return chunks;
    }
    /**
     * @see {@link Compiler}
     * @private
     */
    generate(vars) {
        let res = '';
        let chunks = this.chunk();
        chunks.forEach((chunk) => {
            if (chunk instanceof FunctionCompiler) {
                res += this.render.interpolate(chunk, vars);
            } else {
                res += this.render.addChunk(chunk);
            }
        });
        return super.generate(vars, res);
    }
}
