import { Parser } from './parser.js';

/**
 * Iterate a key/value list of attributes.
 * @private
 * @param {Object} props The list of attributes.
 * @param {Function} callback The callback to trigger for any key/value couple.
 */
function iterateProps(props, callback) {
    if (props) {
        // eslint-disable-next-line
        for (let k in props) {
            callback(k, props[k]);
        }
    }
}

function handleValue(value) {
    if (value === true) {
        value = '';
    }
    if (typeof value === 'string') {
        value = `'${value}'`;
    }
    return value;
}

/**
 * Convert a key/value attributes list into IncrementalDOM format.
 * @private
 * @param {Object} props The list of attributes.
 * @return {Array} The attributes in IncrementalDOM format.
 */
function handleProps(props) {
    let res = [];
    iterateProps(props, (key, value) => {
        res.push(`'${key}'`, handleValue(value));
    });
    return res;
}

export class Render {
    /**
     * A function chunks builder.
     * @param {Object} globals A list of global variable names.
     */
    constructor(globals) {
        this.invocations = [];
        this.$ = globals;
    }

    addChunk(chunk) {
        let res = '';
        let parser = new Parser(chunk);
        let data = parser.next();
        while (data) {
            switch (data.type) {
            case Parser.OPEN_ELEMENT:
                if (data.continue) {
                    res += this.genElementOpenStart(data);
                    this.setElementOpen(data.tag);
                } else {
                    res += this.genElementOpen(data);
                }
                break;
            case Parser.OPEN_ELEMENT_END:
                res += this.genElementOpenEnd(data);
                this.clearElementOpen();
                break;
            case Parser.CLOSE_ELEMENT:
                res += this.genElementClose(data);
                break;
            case Parser.CONTENT:
                if (this.isElementOpen()) {
                    let props = Parser.convertProps(data.content);
                    if (props) {
                        iterateProps(props, (key, value) => {
                            if (key !== 'undefined' || value !== false) {
                                if (key === 'undefined') {
                                    if (this.hasLastAttr()) {
                                        res += this.fillLastAttr(handleValue(value));
                                    }
                                } else if (value === false) {
                                    res += this.setLastAttr(`'${key}'`);
                                } else {
                                    res += this.addProperty(`'${key}'`, handleValue(value));
                                }
                            }
                        });
                    }
                } else {
                    res += this.addText(data.content);
                }
            }
            data = parser.next();
        }
        return res;
    }

    invokeDOM() {
        this.invocations.forEach((val, index) => {
            this.invocations[index] = true;
        });
    }

    queueInvocation() {
        return this.invocations.push(false) - 1;
    }

    checkInvocation(index) {
        return this.invocations[index];
    }

    interpolate(chunk, vars) {
        this.invokeDOM();
        let invocation = this.queueInvocation();
        let fn = `(${chunk.generate(vars)}).apply(${this.$.scope}, ${this.$.args})\n`;
        if (!this.checkInvocation(invocation)) {
            if (this.isElementOpen()) {
                if (this.hasLastAttr()) {
                    return this.fillLastAttr(fn);
                } else {
                    this.setLastAttr(fn);
                }
                return '';
            } else {
                return `${this.$.helper}.text(${fn});`;
            }
        }
        return fn;
    }

    genElementOpen(data) {
        this.invokeDOM();
        let res = '';
        let props = handleProps(data.props);
        if (data.selfClosing) {
            res += `${this.$.helper}.elementVoid('${data.tag}'${props.length ? `, null, null, ${props.join(', ')}` : ''});`;
        } else {
            res += `${this.$.helper}.elementOpen('${data.tag}'${props.length ? `, null, null, ${props.join(', ')}` : ''});`;
        }
        return res;
    }

    genElementOpenStart(data) {
        this.invokeDOM();
        let res = `${this.$.helper}.elementOpenStart('${data.tag}');`;
        if (data.props) {
            iterateProps(data.props, (key, value) => {
                if (value === false) {
                    res += this.setLastAttr(`'${key}'`);
                    delete data.props[key];
                } else {
                    res += `${this.$.helper}.attr('${key}', ${handleValue(value)});`;
                }
            });
        }
        return res;
    }

    genElementOpenEnd(data) {
        this.invokeDOM();
        let res = `${this.$.helper}.elementOpenEnd();`;
        let open = this.getElementOpen();
        if (data.selfClosing || Parser.isSelfClosing(open)) {
            if (!data.tag) {
                data.tag = open;
            }
            res += this.genElementClose(data);
        }
        return res;
    }

    genElementClose(data) {
        this.invokeDOM();
        this.clearLastAttr();
        return `${this.$.helper}.elementClose('${data.tag}');`;
    }
    /**
     * Generate a function chunk for text node creation.
     * @param {String} content The chunk which generates the text node content.
     * @return {String} The function chunk for text node creation.
     */
    addText(content) {
        this.invokeDOM();
        return `${this.$.helper}.text('${content}');`;
    }
    /**
     * Generate a function chunk for attribute setting.
     * @param {String} attrName The chunk which generates the attribute name.
     * @param {String} val The chunk which generates the attribute value.
     * @return {String} The function chunk for attribute setting.
     */
    addProperty(attrName, val) {
        this.invokeDOM();
        return `${this.$.helper}.attr(${attrName}, ${val});`;
    }
    /**
     * Set a pending attribute.
     * @param {String} name The name of the pending attribute.
     */
    setLastAttr(name) {
        let res = '';
        this.lastAttr = name;
        return res;
    }
    /**
     * Set a the value of the pending attribute.
     * @param {String} val A function chunk which generates the value for the pending attribute.
     * @return {String} The function chunk which set the pending attribute.
     */
    fillLastAttr(val) {
        let lastAttr = this.getLastAttr();
        this.clearLastAttr();
        return this.addProperty(lastAttr, val);
    }
    /**
     * Retrieve the pending attribute name.
     */
    getLastAttr() {
        return this.lastAttr;
    }
    /**
     * Check if there is a pending attribute.
     * @return {Boolean}
     */
    hasLastAttr() {
        return !!this.lastAttr;
    }
    /**
     * Clear pending attribute.
     */
    clearLastAttr() {
        this.lastAttr = null;
    }
    /**
     * Set a pending element definition.
     * @param {String} tag The tag of the element.
     */
    setElementOpen(tag) {
        this.lastElement = tag;
    }
    /**
     * Retrieve the pending element definition tag.
     */
    getElementOpen() {
        return this.lastElement;
    }
    /**
     * Check if there is a pending element definition.
     * @return {Boolean}
     */
    isElementOpen() {
        return !!this.lastElement;
    }
    /**
     * Clear pending element definition.
     */
    clearElementOpen() {
        this.lastElement = null;
    }
}
