import {
    skip,
    skipNode,
    importNode,
    currentElement,
    currentPointer,
    elementOpen,
    elementOpenStart,
    elementOpenEnd,
    elementClose,
    elementVoid,
    attr,
    text,
    applyAttr,
    applyProp,
    patch,
    patchOuter,
    symbols,
    attributes,
    notifications,
} from 'incremental-dom';

/**
 * IncrementalDOM wrapper.
 */
export class IDOM {
    /**
     * @private
     */
    static close(...args) { return close(...args); }
    /**
     * @private
     */
    static skip(...args) { return skip(...args); }
    /**
     * @private
     */
    static skipNode(...args) { return skipNode(...args); }
    /**
     * @private
     */
    static importNode(...args) { return importNode(...args); }
    /**
     * @private
     */
    static currentElement(...args) { return currentElement(...args); }
    /**
     * @private
     */
    static currentPointer(...args) { return currentPointer(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpen IncrementalDOM.elementOpen}
     */
    static elementOpen(...args) { return elementOpen(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpenStart IncrementalDOM.elementOpenStart}
     */
    static elementOpenStart(...args) { return elementOpenStart(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpenEnd IncrementalDOM.elementOpenEnd}
     */
    static elementOpenEnd(...args) { return elementOpenEnd(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementClose IncrementalDOM.elementClose}
     */
    static elementClose(...args) { return elementClose(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementVoid IncrementalDOM.elementVoid}
     */
    static elementVoid(...args) { return elementVoid(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/attr IncrementalDOM.attr}
     */
    static attr(...args) { return attr(...args); }
    /**
     * Wrap the IncrementalDOM.text function with some arguments checks.
     * @see {@link http://google.github.io/incremental-dom/#api/text IncrementalDOM.text}
     * @param {String} str The text to render.
     */
    static text(str) {
        if (typeof str === 'undefined') {
            str = '';
        }
        text(str);
    }
    /**
     * @private
     */
    static applyAttr(...args) { return applyAttr(...args); }
    /**
     * @private
     */
    static applyProp(...args) { return applyProp(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/patch IncrementalDOM.patch}
     */
    static patch(...args) { return patch(...args); }
    /**
     * @private
     */
    static patchOuter(...args) { return patchOuter(...args); }
    /**
     * @private
     */
    static get symbols() { return symbols; }
    /**
     * @private
     */
    static get attributes() { return attributes; }
    /**
     * @private
     */
    static get notifications() { return notifications; }
}
